.top-bar {
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo-link {
    line-height: 0;
  }

  &__logo {
    height: 38px;

    @include on-tablet {
      height: 56px;
    }
  }

  &__icons {
    display: flex;
    gap: 24px;
  }
}
